<template>
<v-app style="background-color: rgb(247, 247, 247);">
    <div style="text-align:center;font-size:30px;margin-top:30px;font-family:'bgtitlefamily';"> {{ title }} </div>
    <v-card v-if="paperType=='考试'"
    style="text-align:center;font-size:20px;width:10vw;margin-left:45%;margin-top:10px;font-family:'bgtitlefamily';">
        <h1 v-if="minutes>=10&&seconds<10">{{minutes}}:0{{seconds}}</h1>
        <h1 v-else-if="minutes<10&&seconds>=10">0{{minutes}}:{{seconds}}</h1>
        <h1 v-else-if="minutes<10&&seconds<10">0{{minutes}}:0{{seconds}}</h1>
        <h1 v-else>{{minutes}}:{{seconds}}</h1></v-card>
    <div style="margin: 20px 200px;">
      <v-card>
        <v-card-text style="width:40vw;text-align:left;
       font-size:20px;margin-top:30px;padding-top:20px;font-family:'bgtitlefamily';">{{paperDescription}}</v-card-text>
        <div v-for="(item, index) in question" :key="index" style="margin:10px 40px;">
          <div v-if="index == 0" style="height:20px;"></div>
          <div style="font-size:20px;position:relative;top:10px;">
            <span style="color:red;" v-if="item.necessary==true">* </span>
            <span v-if="showSeq==true||showSeq=='true'">{{ (page-1)*4+index + 1 + '. ' }}</span>  {{ item.title }}
          </div>
          <p style="margin-top:1vh;margin-left:1.5vw;">{{ item.description}}</p>
          <div v-if="item.type == '多选题'||item.type == '多选投票题'||item.type == '多选报名题'">
            <v-container>
              <div v-for="(subItem, i) in item.choices"
                    :key="i">
                <v-checkbox v-model="item.Answer" :label="subItem" hide-details
                :value="subItem" @click="checkBox(item)" disabled
                v-if="item.type== '多选报名题'&&parseInt(item.numLimit[i])==parseInt(item.selectNum[i])"
                >
                <template v-slot:label>
                  <div  style="width:30vw;">
                    {{subItem}}
                  </div>
                </template>
                </v-checkbox>
                <v-checkbox v-model="item.Answer" :label="subItem" hide-details
                :value="subItem" @click="checkBox(item)" v-else
                >
                <template v-slot:label>
                  <div  style="width:30vw;">
                    {{subItem}}
                  </div>
                </template>
                </v-checkbox>
                <p v-if="item.ptype==1" style="float:right;margin-top:-2.7vh;margin-right:26.2vw;color:black;" >{{item.voteNum[i]}}票</p>
                <p v-else-if="item.ptype==0" style="float:right;margin-top:-5vh;margin-right:26.2vw;color:black;" >已报名{{item.selectNum[i]}}人(剩余{{item.numLimit[i]-item.selectNum[i]}}人)</p>
              </div>
            </v-container>
          </div>
          <div v-if="item.type == '打分题'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <v-rating
                  v-model="item.answer" style="margin-left:2vw;"
                  :length="item.limit"
                  color="yellow lighten-3"
                  background-color="grey lighten-1"
                  mid
              ></v-rating>
              </div>
            </div>
          <div v-if="item.type == '单选题'||item.type == '单选投票题'||item.type == '单选报名题'">
            <v-container>
               <v-radio-group v-model="item.answer">
                 <div v-for="(subItem, i) in item.choices" :key="i">
                <v-radio v-if="item.type== '单选报名题'&&parseInt(item.numLimit[i])==parseInt(item.selectNum[i])"
                     :value="subItem" disabled
                ><template v-slot:label>
                  <div style="width:30vw;">
                    {{subItem}}
                  </div>
                </template></v-radio>
                <v-radio v-else style="height:40px;"
                     :value="subItem"
                  ><template v-slot:label>
                    <div style="width:30vw;height:40px;line-height:40px;">
                      {{subItem}}
                    </div>
                  </template>
                </v-radio>
                <p v-if="item.ptype==1" style="float:right;margin-top:-5.2vh;margin-right:26.2vw;color:black;position:relative;top:-5px;" >{{item.voteNum[i]}}票</p>
                <p v-else-if="item.ptype==0" style="float:right;margin-top:-5vh;margin-right:26.2vw;color:black;" >已报名{{item.selectNum[i]}}人(剩余{{item.numLimit[i]-item.selectNum[i]}}人)</p>
                </div>
              </v-radio-group>
            </v-container>
          </div>
          <div v-if="item.type == '填空题'">
            <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
              <v-text-field
                  v-model="item.answer"
                  label="请输入你的答案"
                  clearable
              ></v-text-field>
            </div>
          </div>
          <div v-if="item.type == '文本题'">
            <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
              <v-textarea style="width:30vw;margin-left:0vw;margin-top:1vh;" dense
                  v-model="item.answer"
                  label="请输入你的答案"
                  clearable
              ></v-textarea>
            </div>
          </div>
          <div v-if="item.type == '定位题'">
              <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
                <FillLocation v-model="question[index]" v-if="item.type==='定位题'"></FillLocation>
              </div>
            </div>
          <div v-if="item.type == '下拉框'">
            <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
              <v-select
                  v-model="item.answer"
                  :items="item.choices"
                  item-text="option"
                  item-value="abbr"
                  label="请选择你的选择"
                  persistent-hint
                  return-object
                  single-line
              ></v-select>
            </div>
          </div>
          <div v-if="item.type == '时间选择器'">
            <div style="display:inline-block;width:400px;margin-left:20px;margin-top:20px;">
            <v-menu
            v-model="item.menu "
            :close-on-content-click="false"
            offset-y
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="item.date"
                append-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
                outlined
                dense
                background-color="white"
                style="width: 12vw; margin-left: 0"
              ></v-text-field>
            </template>
            <v-date-picker
            v-model="item.date" @input="item.menu = false"></v-date-picker>
          </v-menu>
          <v-menu
          v-model="item.menu1"
          ref="menu"
          :close-on-content-click="false"
          :nudge-right="40"
          :return-value.sync="time"
          offset-y
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="item.time"
              append-icon="mdi-clock-time-four-outline"
              readonly
              v-bind="attrs"
              v-on="on"
              outlined
              background-color="white"
              dense
              style="width: 12vw;float:right;margin-top:-9.1vh;"
            ></v-text-field>
          </template>
          <v-time-picker
            v-model="item.time"
            full-width
            @click:minute="item.menu1 =false"
          ></v-time-picker>
        </v-menu>
            </div>
          </div>
        </div>
        <div style="text-align:center;">
          <v-btn color="warning" @click="lastPage"
                 style="width:100px;margin:30px 2vw 2vh 2vw;color:white;">上一页</v-btn>
          <v-btn @click="submit"
                 style="background-color:skyblue;width:100px;margin:30px 2vw 2vh 2vw;color:white;">提&nbsp;&nbsp;交</v-btn>
          <v-btn color="warning" @click="nextPage"
                 style="background-color:skyblue;width:100px;margin:30px 2vw 2vh 2vw;color:white;">下一页</v-btn>
        </div>
      </v-card>
    </div>
  </v-app>
</template>

<script>
import "@/assets/font/font.css";
import VoteRadio from "@/components/Vote/VoteRadio";
import VoteCheck from "@/components/Vote/VoteCheck";
import FillLocation from "@/components/FillBlank/FillLocation";
export default {
    name: 'Answer',
    components:{
     VoteRadio,VoteCheck,FillLocation
    },
    data() {
        return {
            id: '',
            title: '这是一个调查',
            paperDescription:'这是一个调查的问卷简介',
            minutes:0,
            timer:'',
            showSeq:false,
            seconds:0,
            page:1,
            id:-1,
            paperType:'',
            menu:'',
            time:'',
            totalPage:1,
            questions:[],
            question: [
            ],
        }
    },
  mounted(){
    console.log(this.$route.path);
    if(localStorage.getItem("id")==-1){
      this.$router.repalce({path:'/login',query:{flag:1}})
      return
    }
    this.id=this.$route.params.id
    this.getData()
    this.question=[]
    console.log(this.question,11);
  },
  methods: {
    getTime(time){
      this.$refs.menu.save(time)
      return time
    },
    setClock(){
        if(this.seconds>0){
          this.seconds--
        }
        else if(this.minutes==0&&this.seconds==0){
            clearInterval(this.timer);
            // this.$axios({url:'/answer/submit',method:'post',data:{
            //   id:this.id,
            //   user_id:parseInt(localStorage.getItem("id")),
            //   answerList:this.questions,
            // }})
            //   .then((res) => {
            //     console.log(res);
            //     if(res.data.status==true) {
            //       this.$router.push('/exam/result/'+this.id)
            //     }
            //     else this.$message.error({ type: "error", message: res.data.exec });
            //   })
            //   .catch((err) => {
            //     console.log(err);
            //     this.$message.error({ type: "error", message: "网络错误" });
            //   });
        }
        else if(this.seconds==0){
          this.seconds=59
          if(this.minutes!=0)this.minutes--
        }
    },
    checkBox(item){
      console.log(item);
      console.log(item.Answer.length);
      console.log(item.limit);
      if(item.Answer.length>item.limit&&this.paperType!='考试'){
        this.$message.warning('此题最多选'+item.limit+'项')
        item.Answer.pop()
      }
    },
    getTime(now,endTime){
      var array1=now.split('')
      var array2=endTime.split('')
      var d1=parseInt(array1[8]+array1[9])
      var h1=parseInt(array1[11]+array1[12])
      var m1=parseInt(array1[14]+array1[15])
      var s1=parseInt(array1[17]+array1[18])
      var d2=parseInt(array2[8]+array2[9])
      var h2=parseInt(array2[11]+array2[12])
      var m2=parseInt(array2[14]+array2[15])
      var s2=parseInt(array2[17]+array2[18])
      console.log(d1,h1,m1,s1,d2,h2,m2,s2);
      var second1=d2*24*3600+h2*60*60+m2*60+s2-d1*24*3600-h1*3600-m1*60-s1
      this.seconds=second1%60
      this.minutes=(second1-this.seconds)/60
      this.timer = setInterval(()=>{this.setClock()}, 1000);
    },
    getData(){
      this.$axios({url:'/answer/get',method:'get',params:{
        id:this.id,
        user_id:parseInt(localStorage.getItem("id")),
      }})
        .then((res) => {
          console.log(res);
          if(res.data.status==true) {
            if(res.data.pstatus==2){
              this.$router.replace('/endresult')
            }
            else if(res.data.pstatus==0){
              this.$router.replace('/begin')
            }
            else if(res.data.count>=1&&res.data.isSingle==true){
              this.$router.replace('/endresult')
              this.$message.warning('您已填写过该问卷')
            }
            for(var i=0;i<res.data.question.length;i++){
              res.data.question[i].date=''
              res.data.question[i].time=''
              res.data.question[i].Answer=[]
              res.data.question[i].answer=''
              res.data.question[i].menu=false
              res.data.question[i].menu1=false
            }
            if(res.data.paperType=='考试'){
                this.getTime(res.data.now,res.data.endTime)
            }
            this.questions=res.data.question
            this.title=res.data.title
            this.showSeq=res.data.showSeq
            this.paperType=res.data.paperType
            this.paperDescription=res.data.paperDescription
             if(this.questions.length%4==0) this.totalPage=this.questions.length/4
            else if(this.questions.length%4!=0) this.totalPage=parseInt(this.questions.length/4)+1
            for(var i=0;i<4&&i<this.questions.length;i++)  this.question.push(this.questions[i])
            console.log(this.questions);
          }
          else this.$message.error({ type: "error", message: res.data.exec });
        })
        .catch((err) => {
          console.log(err);
          this.$message.error({ type: "error", message: "网络错误" });
        });
    },
    submit(){
      for(var i=0;i<this.questions.length;i++){
        if(this.questions[i].type.indexOf('多选')!=-1){
          for(var j=0;j<this.questions[i].Answer.length;j++){
            this.questions[i].answer+=this.questions[i].Answer[j]+','
          }
        }
        else if(this.questions[i].type=='时间选择器'){
          this.questions[i].answer=this.questions[i].date+' '+this.questions[i].time+':00'
        }
        else{
          if(this.questions[i].necessary==true&&this.questions[i].answer==''){
            this.$message.error('第'+(i+1)+'题为必答题,请作答')
            return
          }
        }
      }
      this.$axios({url:'/answer/submit',method:'post',data:{
        id:this.id,
        user_id:parseInt(localStorage.getItem("id")),
        answerList:this.questions,
      }})
        .then((res) => {
          console.log(res);
          if(res.data.status==true) {
            if(this.paperType=='考试')  this.$router.push('/exam/result/'+this.id)
            else this.$router.push('/result')
          }
          else this.$message.error({ type: "error", message: res.data.exec });
        })
        .catch((err) => {
          console.log(err);
          this.$message.error({ type: "error", message: "网络错误" });
        });
    },
    lastPage(){
      if(this.page==1) {
        this.$message.warning('这已经是第一页了')
      }
      else{
        this.question=[]
        this.page--
        for(var i=(this.page-1)*4;i<this.page*4&&i<this.questions.length;i++){
          this.question.push(this.questions[i])
        }
      }
    },
    nextPage(){
      if(this.page==this.totalPage) {
        this.$message.warning('这已经是最后一页了')
      }
      else{
        this.question=[]
        this.page++
        for(var i=(this.page-1)*4;i<this.page*4&&i<this.questions.length;i++){
          this.question.push(this.questions[i])
        }
      }
    }
  }
}
</script>
<style scoped>
</style>