<template>
  <div >
      <h3 style="margin-bottom:0.5vh;">
        <span style="color:red;" v-if="question.necessary==true">* </span>
        {{question.title}}
      </h3>
      <p>{{ question.description}}</p>
      <div v-for="(item,i) in question.choices " :key="i" style="width:45vw;">
        <v-checkbox
          style="margin-top:1vh;"
           dense hide-details>
          <template v-slot:label>
            <div  style="width:30vw;">
              {{item}}
            </div>
          </template>
        </v-checkbox>
        <span v-if="ptype1==1" style="float:right;margin-top:-4.2vh;color:black;" >0票</span>
        <span v-else-if="ptype1==0" style="float:right;margin-top:-3.5vh;color:black;" >已报名0人(剩余0人)</span>
      </div>
  </div>
</template>

<script>
import "@/assets/font/font.css"
export default {
  name: "FillBlank",
  props:{
    value: {
      type: Object,
    },
    ptype:{
      type:Number
    }
    },
    mounted(){
        this.question=this.value
        this.ptype1=this.$props.ptype
    },
    watch:{
      ptype:function (v,o) {
        this.ptype1=v
      }
    },
  model: {
    prop: "value",
    event: "selection",
  },
  data(){
    return{
      question:{},
      ptype1:0
    }
  },
  methods:{
    update() {
      this.$emit("selection",this.question);
      console.log(this.question);
    },
  }
}
</script>
