<template>
  <div >
      <h3 style="">
        <span style="color:red;" v-if="question.necessary==true">* </span>
        {{question.title}}
      </h3>
      <p>{{ question.description}}</p>
      <v-radio-group style=""
        column
        @change="update">
        <div v-for="(item,i) in question.choices " :key="i" style="width:45vw;">
          <v-radio style="margin-top:0vh;" :value="item">
            <template v-slot:label>
              <div  style="width:30vw;">
                {{item}}
              </div>
            </template>
          </v-radio>
          <span v-if="ptype1==1" style="float:right;margin-top:-3.8vh;color:black;" >0票</span>
          <span v-else-if="ptype1==0" style="float:right;margin-top:-3.8vh;color:black;" >已报名0人(剩余0人)</span>
        </div>
      </v-radio-group>
  </div>
</template>

<script>
import "@/assets/font/font.css"
import '@mdi/font/css/materialdesignicons.css'
export default {
  name: "FillBlank",
  props:{
    value: {
      type: Object,
    },
    ptype:{
      type:Number
    }
    },
    watch:{
      ptype:function (v,o) {
        this.ptype1=v
      }
    },
    mounted(){
        this.question=this.value
        this.ptype1=this.$props.ptype
    },
  model: {
    prop: "value",
    event: "selection",
  },
  data(){
    return{
      question:{},
      ptype1:0
    }
  },
  methods:{
    update() {
      this.$emit("selection",this.question);
      console.log(this.question);
    },
  }
}
</script>
